 
<template>
    <section class="main-content">
        <div class=" padding-t_10">

            <div class="col-md-12 p-0">
                <div class="theme-primary partition  p-actions-expand">
                    <span class="p-name text-white textelipsis">
                        Scanned Items Status
                    </span>
                </div>
            </div>
            <div class="row">
                <div class="col-12 mt-2">
                    <div class="mr-2 d-inline-block">
                        <span class="legends_profile colorboxsquare"></span> Missing Products
                    </div>
                </div>
            </div>
            <div class="listing mt-2" id="detailTable" v-if="GroupName.length != 0">
                <div v-for="(ParentItem, ParentIndex) in GroupName" :key="ParentIndex">
                    <div class="accordion custom-accr">
                        <div class="card">
                            <div class="card-header" role="tab" id="headingTwo2">
                                <a data-toggle="collapse" data-parent="#accordionEx" class="collapsed font-dark">
                                    <em class="fa fa-angle-down rotate-icon" aria-hidden="true"></em>{{
                                        ParentItem.groupData[0].device_name }}( {{
        ParentItem.groupKey }})
                                </a>
                            </div>
                            <div class="collapse show" role="tabpanel" data-parent="#accordionEx">
                                <div class="card-body py-0 pb-3 px-0">
                                    <div class="table-responsive border-0">
                                        <div id="example_wrapper" class="dataTables_wrapper no-footer">
                                            <table id="example"
                                                class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                                                style="width: 100%;" role="grid" aria-describedby="example_info">
                                                <thead class="thead-bg">
                                                    <tr role="row">
                                                        <th><span>Location</span></th>
                                                        <th><span>Product Type</span></th>
                                                        <th><span>Product Name</span></th>
                                                        <th><span>Unique Code</span></th>
                                                        <th><span>Sub Location</span></th>
                                                        <th><span>Serial Number</span></th>
                                                        <th><span>EPC Tags</span></th>
                                                        <th><span>IP Address</span></th>
                                                        <th><span>Status</span></th>
                                                        <th class="text-center"><span>Actions</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr colspan="20" class="text-center text-danger no-record"
                                                        v-if="GroupName.length == 0"> {{ $t('NoRecordfound') }} </tr>
                                                    <tr v-for="(ChildItem, ChildIndex) in ParentItem.groupData"
                                                        :key="ChildIndex">
                                                        <td>{{ ChildItem.LOCATION_NAME }}</td>
                                                        <td>{{ ChildItem.ASSET_TYPE }}</td>
                                                        <td>{{ ChildItem.unique_name }}</td>
                                                        <td>{{ ChildItem.ITEM_CODE }}</td>
                                                        <td>{{ ChildItem.sub_location }}</td>
                                                        <td>{{ ChildItem.serial_number }}</td>
                                                        <td>{{ ChildItem.EPC_TAG }}</td>
                                                        <td>{{ ChildItem.ip_address }}({{ ChildItem.device_name }})</td>
                                                        <td>{{ ChildItem.STATUS_NAME }}</td>
                                                        <td class="text-center">
                                                            <a href="" title="Edit" style="cursor:pointer;" id="Edit">
                                                                <em
                                                                    class="fa fa-pencil text-success mx-1 action-icon enable"></em>
                                                            </a>
                                                            <a href="" id="" title="Assign" class="d-none"><em
                                                                    class="fa fa-user pr-2 text-primary action-icon mx-1"></em></a>
                                                            <a href="javascript:;" title="Location"
                                                                data-locationId="@item1.LocationId">
                                                                <em
                                                                    class="fa fa-map-marker mx-1 text-warning action-icon"></em>
                                                            </a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="listing mt-2" id="detailTable" v-if="GroupName.length == 0">

                <div class="accordion custom-accr">
                    <div class="card">
                        <div class="collapse show" role="tabpanel" data-parent="#accordionEx">
                            <div class="card-body py-0 pb-3 px-0">
                                <div class="table-responsive border-0">
                                    <div id="example_wrapper" class="dataTables_wrapper no-footer">
                                        <table id="example"
                                            class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                                            style="width: 100%;" role="grid" aria-describedby="example_info">
                                            <thead class="thead-bg">
                                                <tr role="row">
                                                    <th><span>Location</span></th>
                                                    <th><span>Product Type</span></th>
                                                    <th><span>Product Name</span></th>
                                                    <th><span>Unique Code</span></th>
                                                    <th><span>Sub Location</span></th>
                                                    <th><span>Serial Number</span></th>
                                                    <th><span>EPC Tags</span></th>
                                                    <th><span>IP Address</span></th>
                                                    <th><span>Status</span></th>
                                                    <th class="text-center"><span>Actions</span></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-if="GroupName.length == 0">
                                                    <td class="text-center text-danger no-record" colspan="20"> {{
                                                        $t('NoRecordfound') }} </td>
                                                </tr>

                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

        </div>

    </section>
</template>
<script>
import DataService from '../../service/DataService'
export default {
    data() {
        return {
            isLoading: false,
            Scanneditemsdata: [],
            searchdata: '',
            locationId: '',
            pageSize: 100,
            pageNum: 1,
            sortBy: '',
            sortExp: '',
            ePC_TAG: '',
            GroupName: []
        }
    },
    created() {

        this.fetchData();
    },
    methods: {
        fetchData() {

            var vueObj = this;
            var params = `search=${vueObj.searchdata}&locationId=${vueObj.locationId}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&ePC_TAG=${vueObj.ePC_TAG}`;
            DataService.GetScanneditemsData(params).then(response => {
                debugger
                if (response.data != '' && response.data != null) {

                    vueObj.Scanneditemsdata = response.data
                    vueObj.GroupName = [];
                    var gruopBy = "";
                    $.each(vueObj.Scanneditemsdata, $.proxy(function (index, item) {
                        gruopBy = item.ip_address;
                        var chkGroup = $.grep(vueObj.GroupName, function (element, index) {
                            return element.groupKey == gruopBy;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vueObj.Scanneditemsdata, function (element, index) {
                                return element.ip_address == gruopBy;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    groupKey: gruopBy,
                                    groupData: returnedData,
                                };
                                vueObj.GroupName.push(objGroup);
                            }
                        }
                    }, this));
                    console.log('dsfgfdsg', vueObj.GroupName)
                }
            }).catch(err => {
                console.log(err)
            })
        }
    }
}
</script>

